.default_theme{--color1: #CAF0F8;--color2: #ADE8F4;--color3: #90E0EF;--color4: #48CAE4;--color5: #00B4D8;--color6: #0096C7;--color7: #0077B6;--color8: #023E8A;--color9: #03045E}.greyscale_theme{--color1: #eeeeee;--color2: #cccccc;--color3: #999999;--color4: #666666;--color5: #333333;--color6: #000000;--color7: #000000;--color8: #000000;--color9: #000000}.pilgrim_theme{--color1: #CBC6B1;--color2: #B49C7A;--color3: #B8A06D;--color4: #967047;--color5: #71593E;--color6: #594840;--color7: #594840;--color8: #594840;--color9: #594840}.theme{--bgColorMain: var(--color1);--headerColor: var(--color2);--fontColorMain: var(--color6);--fontColorAlt: var(--color3);--fontFamily: "Arvo", Times, sans-serif;--borderStyle: 2px solid var(--color3);--borderColorAlt: var(--color4);--menuBgMain: var(--color3);--menuBgAlt: var(--color2);--menuBgHover: var(--color1);--linkColorMain: var(--color5);--linkColorHover: var(--color4);--paddingPageContent: 10px}a,a:visited{color:var(--linkColorMain)}a:hover{color:var(--linkColorHover)}h1,h2,h3,h4,h5,h6,p{color:var(--fontColorMain);padding:0px;margin:0px}.layout{width:100%;height:100%;background-color:var(--bgColorMain);color:var(--fontColorMain);font-family:var(--fontFamily);display:grid;grid-template-rows:90px auto;grid-template-areas:"header" "page_content"}.layout.collapsed{grid-template-rows:calc(100% - 20px)}.site-header{grid-area:header;background-color:var(--headerColor);border-bottom:var(--borderStyle)}.site-header.collapsed{display:none}.collapse-header{width:100%;height:20px;position:absolute;top:70px;display:grid;justify-content:center;align-content:center}.collapse-header.collapsed{top:0px;z-index:1}.collapse-icon{font-size:3.3em;cursor:pointer}.collapse-icon:hover{color:var(--fontColorHover)}div.select-theme{position:absolute;right:0px;top:0px;z-index:1;width:100px}h5.select-theme{text-align:center;color:var(--fontColorAlt)}.drop-down{cursor:pointer;width:100px;position:absolute;right:0px;border:1px solid var(--borderColorAlt)}.drop-down-placeholder{background-color:var(--menuBgMain)}.drop-down-menu{background-color:var(--menuBgAlt)}.menu-item:hover{background-color:var(--menuBgHover)}.page-content-wrapper{grid-area:"page_content";width:100%;height:100%;overflow:hidden;position:relative}.page-content-wrapper.collapsed{height:100%;margin-top:20px}.page-content{padding:var(--paddingPageContent);position:absolute;top:0;bottom:0;left:0;right:-17px;overflow-y:scroll}